html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-rubik);
  line-height: 1.5;
  font-size: 1rem;
  background-color: var(--ei);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --alpha: hsl(229, 8%, 60%);
  --beta: hsl(229, 31%, 21%);
  --gamma: hsl(0, 94%, 66%);
  --delta: hsl(231, 69%, 60%);
  --ei: #ffff;
}

:root {
  --font-sans: "Open Sans", sans-serif;
  --font-rubik: "Rubik", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 2.125rem;
}

h2 {
  font-size: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  color: var(--beta);
  font-weight: 500;
  margin: 3.4375rem 12.5rem;
}
.nav__logo {
  display: flex;
  align-items: center;
}
.nav__menu {
  display: flex;
  align-items: center;
}
.nav__menu-item {
  margin-right: 3.125rem;
  letter-spacing: 0.125rem;
  font-size: 0.875rem;
}
.nav__menu-item:last-child {
  margin-right: 0;
}
.nav__menu button {
  background-color: var(--gamma);
  color: var(--ei);
  padding: 0.8125rem 1.75rem;
  border: solid 2px var(--gamma);
  border-radius: 0.3125rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  transition: all 0.5s ease;
  cursor: pointer;
}
.nav__menu button:hover {
  background-color: transparent;
  color: var(--gamma);
  border: solid 2px var(--gamma);
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--alpha);
  margin: 0 9.375rem;
}
.hero__content {
  width: 30%;
  margin-left: 2.5rem;
}
.hero__content h1 {
  letter-spacing: 0.0625rem;
  color: var(--beta);
  margin-bottom: 1.875rem;
  font-weight: 500;
}
.hero__content p {
  margin: 0 0 2.1875rem 0;
}
.hero__buttons button {
  padding: 0.8125rem 1.5625rem;
  border: solid 2px transparent;
  border-radius: 0.3125rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  transition: all 0.5s ease;
  font-weight: 700;
  cursor: pointer;
}
.hero__left-button {
  background-color: var(--delta);
  color: var(--ei);
  margin-right: 0.625rem;
}
.hero__left-button:hover {
  background-color: transparent;
  color: var(--delta);
  border: solid 2px var(--delta);
}
.hero__right-button {
  color: var(--beta);
  background-color: #f7f7f7;
}
.hero__right-button:hover {
  background-color: transparent;
  border: solid 2px var(--beta);
}
.hero__image {
  margin-top: 3.125rem;
}
.hero__bg {
  background-color: var(--delta);
  width: 28.125rem;
  height: 21.875rem;
  border-radius: 0 0 0 50%;
  position: absolute;
  right: 0;
  top: 300px;
  z-index: -2;
}

.features__header-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 2.1875rem;
}
.features__header {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 35%;
}
.features__header h2 {
  color: var(--beta);
  margin-bottom: 0.625rem;
}
.features__header p {
  color: var(--alpha);
}
.features__buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 4.375rem;
}
.features__buttons button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: var(--alpha);
  font-weight: 600;
  padding-bottom: 1.25rem;
}
.features__panel-wrapper {
  margin-left: 10.625rem;
}
.features__tab-panel {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 0;
  height: 0;
  overflow: hidden;
}
.features__tab-panel img {
  margin-right: 3.125rem;
}
.features__tab-text {
  width: 50%;
}
.features__tab-text p {
  width: 70%;
  margin-bottom: 2.1875rem;
  color: var(--alpha);
}
.features__tab-text h2 {
  color: var(--beta);
}
.features__tab-panel button {
  background-color: var(--delta);
  color: var(--ei);
  font-weight: 600;
  padding: 0.9375rem 1.5625rem;
  border: solid 2px var(--delta);
  border-radius: 0.3125rem;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  transition: all 0.5s ease;
}
.features__tab-panel button:hover {
  background-color: transparent;
  border: solid 2px var(--delta);
  color: var(--delta);
}
.features__bg {
  background-color: var(--delta);
  width: 28.125rem;
  height: 21.875rem;
  border-radius: 0 50% 50% 0;
  position: absolute;
  left: 0;
  top: 1055px;
  z-index: -2;
}

.extension {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding-top: 15.625rem;
  color: var(--alpha);
}
.extension h2 {
  color: var(--beta);
}
.extension h3 {
  color: var(--beta);
}
.extension__header {
  width: 30%;
}
.extension__card-wrapper {
  display: flex;
  align-items: inherit;
}
.extension__card {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  border-radius: 0.625rem;
  padding: 3.125rem 2.5rem 1.5625rem;
  margin-right: 2.5rem;
  margin-top: 4.6875rem;
  background-image: url(../../../images/bg-dots.svg);
  background-repeat: no-repeat;
  background-position-y: 18.75rem;
  background-position: center;
}
.extension__card img {
  margin-bottom: 2.1875rem;
}
.extension__card h3 {
  margin-bottom: 1.25rem;
}
.extension__card p {
  margin-bottom: 4.375rem;
}
.extension__card:first-child {
  margin-top: 0;
}
.extension__card:last-child {
  margin-right: 0;
  margin-top: 7.8125rem;
}
.extension button {
  padding: 0.8125rem 1.5625rem;
  border: solid 2px transparent;
  border-radius: 0.3125rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  transition: all 0.5s ease;
  font-weight: 700;
  cursor: pointer;
  background-color: var(--delta);
  color: var(--ei);
}
.extension button:hover {
  background-color: transparent;
  color: var(--delta);
  border: solid 2px var(--delta);
}

.questions {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 9.375rem;
  color: var(--beta);
}
.questions__header {
  text-align: center;
  width: 40%;
  margin-bottom: 3.125rem;
}
.questions__header p {
  color: var(--alpha);
}
.questions__accordion {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 50%;
  margin-bottom: 3.4375rem;
}
.questions__question {
  border-bottom: 0.3px solid var(--alpha);
}
.questions__question:first-child {
  border-top: 0.3px solid var(--alpha);
}
.questions__panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
}
.questions__panel img {
  margin-right: 3.125rem;
}
.questions__panel P {
  font-weight: 500;
}
.questions__content {
  position: relative;
  height: 0;
  overflow: hidden;
  color: var(--alpha);
}
.questions__icon-close {
  display: none;
  margin-right: 3.125rem;
}
.questions button {
  margin-bottom: 9.375rem;
  background-color: var(--delta);
  color: var(--ei);
  font-weight: 600;
  padding: 0.9375rem 1.5625rem;
  border: solid 2px var(--delta);
  border-radius: 0.3125rem;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  transition: all 0.5s ease;
}
.questions button:hover {
  background-color: transparent;
  border: solid 2px var(--delta);
  color: var(--delta);
}
.questions .active {
  height: 100%;
}
.questions .active-color {
  color: var(--gamma);
}
.questions .icon-toggle {
  display: none;
}
.questions .active-icon {
  display: block;
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--delta);
  color: var(--ei);
  text-align: center;
}
.contact p {
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-top: 4.6875rem;
  margin-bottom: 3.125rem;
}
.contact h2 {
  font-size: 1.5625rem;
  margin-bottom: 3.125rem;
}
.contact input {
  padding: 0.8125rem 3.125rem 0.75rem 0.8125rem;
  border-radius: 0.3125rem;
  border: none;
}
.contact input::placeholder {
  color: var(--alpha);
}
.contact button {
  background-color: var(--gamma);
  color: var(--ei);
  font-weight: 600;
  padding: 0.9375rem 1.5625rem;
  border: solid 2px var(--gamma);
  border-radius: 0.3125rem;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  transition: all 0.5s ease;
  margin-bottom: 4.6875rem;
}
.contact button:hover {
  background-color: var(--ei);
  border: solid 2px var(--gamma);
  color: var(--gamma);
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--beta);
  padding: 2.1875rem 10.625rem;
}
.footer__menu {
  display: flex;
  align-items: center;
}
.footer__item {
  margin-right: 1.875rem;
  text-transform: uppercase;
  color: var(--ei);
  letter-spacing: 2px;
  transition: all 0.5s ease;
}
.footer__item:last-child {
  margin-right: 0;
}
.footer__item:hover {
  color: var(--gamma);
}
.footer__social {
  display: flex;
  align-items: center;
}
.footer__social svg {
  margin-right: 1.875rem;
  cursor: pointer;
}
.footer__social svg path {
  transition: all 0.5s ease;
}
.footer__social svg:hover path {
  fill: var(--gamma);
}